<template>
    <div>
        <!-- 登录弹框 -->
        <el-dialog title="登录" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
            <el-form :model="loginForm">
                <el-form-item label="账号">
                    <el-input v-model="loginForm.username" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="密码">
                    <el-input type="password" v-model="loginForm.password" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="login">确 定</el-button>
            </span>
        </el-dialog>
        <el-header style="text-align: left; font-size: 30px">教务管理系统
            <el-button id="login" @click="dialogVisible = true">登录</el-button>
            <el-dropdown>
                <i class="el-icon-setting" style="margin-right: 15px"></i>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item><router-link to="/about" style="text-align: center; font-size: 15px">关于</router-link></el-dropdown-item>
                    <el-dropdown-item><router-link to="/" style="text-align: center; font-size: 15px">表单</router-link></el-dropdown-item>
                    <el-dropdown-item>删除</el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
            
        </el-header>
    </div>
</template>
<script>
export default {
    data() {
        return {

            // 控制弹框的显示
            dialogVisible: false,
            // 登录表单的数据
            loginForm: {
                username: '',
                password: ''
            }

        }
    },
    methods: {
        // 登录方法
        login() {
            if (this.loginForm.username == "student" && this.loginForm.password == "student") {
                this.$message({
                    type: 'success',
                    message: '登录成功!你好,' + this.loginForm.username + "~"
                });
            }
            else if (this.loginForm.username == "teacher" && this.loginForm.password == "teacher") {
                this.$message({
                    type: 'success',
                    message: '登录成功!你好,' + this.loginForm.username + "~"
                });
            }
            else if (this.loginForm.username == "admin" && this.loginForm.password == "admin") {
                this.$message({
                    type: 'success',
                    message: '登录成功!你好,' + this.loginForm.username + "~"
                });
            }
            else {
                this.$message({
                    type: 'error',
                    message: '登录失败!'
                });

            }
            this.dialogVisible = false;
        }
    }
}
</script>
<style>

.el-header {
    font-size: 30px;
    /* 增大字体大小 */
    font-weight: bold;
    /* 加粗字体 */
}



</style>
